import React from 'react';
import { Form, Input, Select, Checkbox, Button, DatePicker, Upload } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import './registrationform.css';

const { Option } = Select;

const RegistrationForm = () => {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    console.log('Form Data Submitted:', values);
    alert('ng k thnh cng!');
  };

  return (
    <div className="registration-form text-white">
      <h2>ng k ti khon</h2>
      <Form form={form} onFinish={onFinish} layout="vertical">
        <Form.Item name="fullName" label="H v tn" rules={[{ required: true, message: 'Vui lng nhp h v tn!' }]}> 
          <Input />
        </Form.Item>

        <Form.Item name="email" label="Email" rules={[{ required: true, type: 'email', message: 'Vui lng nhp email hp l!' }]}> 
          <Input />
        </Form.Item>

        <Form.Item name="phoneNumber" label="S in thoi" rules={[{ required: true, message: 'Vui lng nhp s in thoi!' }]}> 
          <Input />
        </Form.Item>

        <Form.Item name="username" label="Tn ng nhp" rules={[{ required: true, message: 'Vui lng nhp tn ng nhp!' }]}> 
          <Input />
        </Form.Item>

        <Form.Item name="password" label="Mt khu" rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}> 
          <Input.Password />
        </Form.Item>

        <Form.Item name="confirmPassword" label="Xc nhn mt khu" dependencies={['password']} rules={[{ required: true, message: 'Vui lng xc nhn mt khu!' }, ({ getFieldValue }) => ({ validator(_, value) { return !value || getFieldValue('password') === value ? Promise.resolve() : Promise.reject(new Error('Mt khu khng khp!')); }, }),]}> 
          <Input.Password />
        </Form.Item>

        <Form.Item name="birthDate" label="Ngy sinh"> 
          <DatePicker style={{ width: '100%' }} />
        </Form.Item>

        <Form.Item name="gender" label="Gii tnh"> 
          <Select placeholder="Chn gii tnh">
            <Option value="male">Nam</Option>
            <Option value="female">N</Option>
            <Option value="other">Khc</Option>
          </Select>
        </Form.Item>

        <Form.Item name="address" label="a ch"> 
          <Input />
        </Form.Item>

        <Form.Item name="avatar" label="nh i din"> 
          <Upload beforeUpload={() => false} listType="picture">
            <Button icon={<UploadOutlined />}>Ti ln</Button>
          </Upload>
        </Form.Item>

        <Form.Item name="termsAccepted" valuePropName="checked" rules={[{ validator:(_, value) => value ? Promise.resolve() : Promise.reject(new Error('Bn cn ng  vi iu khon s dng!')) }]}> 
          <Checkbox>Ti ng  vi iu khon s dng v chnh sch bo mt.</Checkbox>
        </Form.Item>

        <Form.Item>
          <Button type="primary" htmlType="submit">ng k</Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default RegistrationForm;
